<template>
	<div class="main">
	    <div class="block"></div>
	    <div class="container">
	    	<div class="tits">
	    	  <div class="tits_line">
	    	    <h1><img src="../../images/aboutIcon.jpg"><span>关</span>于我们</h1>
	    		<div class="line"></div>
	    	  </div>
	    	</div>
	    	<div class="content">
	    	  <div class="left">
	    		<ul>
	    			<li 
	    			v-for="(item,index) in tabs" 
	    			:class="{active:index == num}"
	    			@click="tab(index)"><a>{{item}}</a></li>
	    		</ul>
	    	  </div>
	    	  <div class="right">
	    	  	<div 
	    	  	v-for='(itemCon,index) in tabContents' 
	    	  	v-show=" index == num">{{itemCon}}</div>
	    	  </div>
	    	</div>
	    </div>
	</div>
</template>

<script>
 export default{
 	name:'index',
    data(){
    	return{
    	tabs: ["> 企业介绍", "> 企业文化","> 业务范围","> 联系我们"],
        tabContents: ["内容一", "内容二","内容三","内容四"],
        num: 0,
    }
    },
    methods:{
        tab(index) {
            this.num = index;
        }
    }
 }
</script>

<style scoped>
	.main{
		width:100%;
		background:#f1f1f1;
		height:500px;
	}
	.block{
		width:100%;
		height:20px;
		background:#f1f1f1;
	}
	.container{
		width:1200px;
		margin:0px auto;
		background:#ffffff;
		height:450px;
	}
	.tits{
		width:100%;
		height:40px;
	}
	.tits {
		width: 100%;
		height: 40px;
		padding: 48px 0 4px;
		-webkit-box-sizing: content-box;
		box-sizing: content-box;
		background-color: #fff;
	}
	.tits_line{
		width:1160px;
		margin-left:40px;
		border-bottom:1px solid #ebebeb;
	}
	.tits h1 {
		margin: auto;
		font-size: 18px;
		color: #e99014;
		height: 100%;
		line-height: 32px;
		font-weight: 400;
	}
	.tits h1 span{
		font-size:28px;
	}
	.line{
		width:124px;
		height:1px;
		background:#e99014;
		margin:4px 0 0 0px;
	}
	.content{
		padding:0px 70px 0 40px;
	}
	.content .left{
		height:244px;
		width:223px;
		float:left;
	}
	.content li{
		height:60px;
		border-bottom:1px solid #ebebeb;
		line-height:60px;
	}
	.content li a{
		color:#595959;
	}
	.content .right{
		float:right;
		width:799px;
        margin-top:40px;
	}
	.active a{
		color:#e99014 !important;
	}
</style>


